.header {
        width: 100%;
        height: 78px;
        padding-top: 16px;
        background-color: var(--accent-color);
        position: sticky;
        top: 0;
        z-index: 100;

    &__container {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    &__btn {
        width: 176px;
        height: 50px;
        background-color: var(--dark-color);
        border-radius: 18px;
        font-weight: 600;
        color: var(--light-color);

        @include small-tablet {
            display: none;
        }
    }

    &__link {
        color: var(--light-color);
    }
}
