.burger {
    display: none;
    position: relative;
    border: 2px solid var(--dark-color);
    border-radius: 10px;
    width: 44px;
    height: 44px;

    @include small-tablet {
        display: block;
    }

    &__line {
        position: absolute;
        left: 50%;
        top: 50%;
        border-radius: 100px;
        width: 26px;
        height: 3px;
        background-color: var(--dark-color);
        transform: translateX(-50%) translateY(-50%);

        &:first-child {
            margin-top: -7px;
        }

        &:last-child {
            margin-top: 7px;
        }
    }


}

.burger-close {
    display: none;
    position: absolute;
    right: 17px;
    top: 19px;
    border: 2px solid var(--dark-color);
    border-radius: 10px;
    width: 44px;
    height: 44px;
    background-image: url('../img/close.svg');
    background-position: center;
    background-size: 40px 40px;
    background-repeat: no-repeat;

    @include small-tablet {
        display: block;
    }
}
