.app {

    &__container {
        max-width: 920px;
        padding: 0;
        padding-top: 63px;

        @include high-tablet {
            padding: 50px 20px;
        }

        @include small-tablet {
            padding: 20px 20px;
        }
    }

    &__image {
        position: relative;
        margin-bottom: 58px;
    }

    &__title {
        display: none;
    }

    &__text {
        margin: 0;
        position: absolute;
        font-size: 37px;
        line-height: 39px;
        font-weight: 900;
        max-width: 548px;
        bottom: -26px;

        @include high-tablet {
            font-size: 30px;
            max-width: 500px;
            line-height: normal;
        }

        @include small-tablet {
            font-size: 20px;
            max-width: 280px;
            line-height: normal;
        }
    }

    &__link {
        width: 355px;
        height: 80px;
        padding: 26px 58px;
        line-height: 80px;
        background-color: var(--dark-color);
        border-radius: 24px;
        font-size: 24px;
        font-weight: 600;
        color: var(--light-color);

        @include small-tablet {
            width: 200px;
            height: 50px;
            padding: 20px;
            line-height: 50px;
            font-size: 18px;
            font-weight: 600;
        }
    }

    &__links {
        margin: 0;
        position: relative;
        margin-top: 69px;
        font-size: 64px;
        color: var(--light-color);
        letter-spacing: 1.3px;
        line-height: 110%;

        &--text {
            color: var(--light-color);
            text-decoration: underline;
        }

        &::before {
            content: '';
            position: absolute;
            right: 0;
            top: -48px;
            width: 116px;
            height: 77px;
            background-image: url('img/lines.svg');
            background-size: 116px 77px;

            @include small-tablet {
                display: none;
            }
        }

        &::after {
            content: '';
            position: absolute;
            right: 76px;
            bottom: -53px;
            width: 816px;
            height: 35px;
            background-image: url('img/line.svg');
            background-size: 816px 35px;

            @include small-tablet {
                display: none;
            }
        }

        @include small-tablet {
            font-size: 30px;
        }
    }
}
