.gallery {
    &__container {
        max-width: 1106px;
        padding: 0;
        padding-top: 100px;
        padding-left: 152px;
        text-align: right;

        @include high-desktop {
            margin-right: 20px;
        }

        @include high-tablet {
            margin: 0;
            padding: 0;
            padding: 100px 20px 50px;
            max-width: 100%;
            text-align: center;
        }

        @include small-tablet {
            padding: 60px 20px 10px;
        }
    }

    &__video {
        border-radius: 32px;
        width: 734px;

        @include high-tablet {
            max-width: 100%;
        }
    }
}
