.nav {

    &__list {
        display: flex;
        justify-content: space-between;

        @include small-tablet {
            @include flex-all-center;

            flex-direction: column;

            width: 100%;
            height: 100%;
        }
    }

    &__item {
        font-weight: 600;

        &:not(:last-child) {
            margin-right: 14px;

            @include small-tablet {
                margin-right: 0;
                margin-bottom: 30px;
            }
        }
    }



    &__link {
        color: var(--dark-color);

        @include small-tablet {
            font-size: 24px;
        }
    }

    @include high-desktop {
        margin: 0 20px;
    }

    @include small-tablet {
        position: fixed;
        left: 0;
        top: 0;
        margin: 0;
        z-index: 500;
        overflow-y: auto;
        width: 100%;
        height: 100vh;
        background-color: var(--accent-color);
        opacity: 0;
        visibility: hidden;
        transition: opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;
    }
}

.menu--active {
    opacity: 1;
    visibility: visible;
}
