.footer {
    background-color: var(--second-color);

    &__container {
        padding: 58px 0;
        max-width: 920px;
        display: flex;
        justify-content: space-between;

        @include high-tablet {
            flex-direction: column;
            align-items: center;
            padding: 20px;
        }
    }

    &__left {
        max-width: 65%;

        @include high-tablet {
            max-width: 100%;
            text-align: center;
        }
    }

    &__right {
        max-width: 33%;

        @include high-tablet {
            max-width: 100%;
            text-align: center;
        }

        .footer__text {
            @include high-tablet {
                margin-top: 20px;
                max-width: 100%;
            }
        }
    }

    &__text {
        margin: 0;
        font-size: 12px;
        line-height: 133%;
        letter-spacing: 0.16px;
    }

    &__link {
        text-decoration: none;
    }

    &__link:visited {
        color: inherit;
    }
}
