.rent {
    &__container    {
        margin-top: 126px;
        max-width: 954px;
        padding-right: 12px;
        overflow: hidden;

        @include high-tablet {
            margin-top: 50px;
        }
    }

    &__header {
        margin: 0;
        padding-bottom: 93px;
        font-weight: 900;
        font-size: 64px;
        letter-spacing: 1.6px;

        @include high-tablet {
            padding-bottom: 60px;
            font-size: 50px;
            text-align: center;
        }

        @include small-tablet {
            font-size: 36px;
        }
    }

    &__block {
        display: flex;
        justify-content: space-between;
        align-items: flex-start;
        margin-bottom: 91px;

        @include high-tablet {
            flex-direction: column;
            align-items: center;
        }

        @include small-tablet {
            margin-bottom: 40px;
        }
    }

    &__video {
        margin-top: 4px;
        max-width: 360px;

        @include small-tablet {
            max-width: 260px;
        }
    }

    &__text {
        margin: 0 auto;
        padding: 50px 0;
        background-color: var(--dark-color);
        max-width: 100%;
        border-radius: 56px;

        @include high-tablet {
            padding: 30px 0;
            max-width: 70%;
        }

        @include small-tablet {
            padding: 20px 0;
            max-width: 90%;
        }
    }

    &__descr {
        margin: 0 auto;
        color: var(--light-color);
        max-width: 70%;
        line-height: 104%;
        font-size: 42px;
        font-weight: 900;
        text-align: center;

        @include high-tablet {
            font-size: 30px;
        }

        @include small-tablet {
            font-size: 18px;
        }
    }
}
