.rules {
    &__list {
        max-width: 480px;
        padding-right: 40px;

        @include high-tablet {
            max-width: 100%;
            padding-right: 0;
        }
    }

    &__item {
        position: relative;
        display: flex;
        flex-direction: column;
        margin-bottom: 95px;
        font-size: 36px;
        max-width: 455px;
        line-height: 110%;
        letter-spacing: 1.06px;

        &:nth-child(2n) img {
           max-width: 112px;
        }

        &:last-child::after {
            content: '';
            position: absolute;
            right: -294px;
            bottom: -150px;
            width: 345px;
            height: 357px;
            background-image: url('../img/arrow.svg');
            background-size: 345px 357px;

            @include high-tablet {
                display: none;
            }

        }

        @include high-tablet {
            margin-bottom: 40px;
            align-items: center;
            text-align: center;
            font-size: 30px;
        }

        @include small-tablet {
            font-size: 20px;
        }
    }

    &__img {
        margin-bottom: 33px;
        max-width: 362px;
        max-height: 112px;

        @include high-tablet {
            margin-bottom: 15px;
        }

        @include small-tablet {
            max-width: 250px;
            max-height: 77px;
        }
    }
}
